<template>
<div>
<v-btn @click="showMarkerArea"  color="primary" fab large top right absolute style="margin-top:50px">
          <v-icon>mdi-pencil</v-icon>
    </v-btn>
      <v-btn @click="navigateLeft()" v-show="showNavigateLeft" color="primary" fab large bottom absolute left style="margin-bottom:40px; margin-left:20px">
        <v-icon>mdi-arrow-left</v-icon>
   </v-btn>
   <v-btn  @click="navigateRight()" v-show="showNavigateRight"  color="primary" fab large bottom absolute right style="margin-bottom:40px; margin-right:20px">
        <v-icon>mdi-arrow-right</v-icon>
    </v-btn>
   <v-btn  v-show="isLastPage" href="https://vuetifyjs.com/" color="secondary" fab large bottom absolute right style="margin-bottom:40px; margin-right:20px">
        <v-icon> mdi-undo</v-icon>
    </v-btn>
 <div style="height:100%;max-width:auto;background-color:#ad97b8;">
    <img ref="imgRef" :src="getSourcePage"   />
 </div>
  
     
 </div>
</template>

<script>
import * as markerjs2 from 'markerjs2';
export default {
  name: 'Slides',
   data () {
     return {
       source: "/pages/${number}.png",
       pageNumber: 1
       }
    },
    computed: {
    // a computed getter
    getSourcePage: function () {
      if (this.pageNumber < 10){
        console.log("/pages/0${number}.png".replace("${number}", this.pageNumber))
        return "/pages/0${number}.png".replace("${number}", this.pageNumber)
      }else{
        console.log("/pages/${number}.png".replace("${number}", this.pageNumber))
        return "/pages/${number}.png".replace("${number}", this.pageNumber)
      }
    },
    showNavigateRight: function () {
      if (this.pageNumber < 65){
        return true
      }
      return false
    },
    showNavigateLeft: function () {
      if (this.pageNumber > 1){
        return true
      }
      return false
    },
    isLastPage: function () {
      if (this.pageNumber == 65){
        return true
      }
      return false
    }
  },
   methods:{
      showMarkerArea() {
        // create a marker.js MarkerArea
        const markerArea = new markerjs2.MarkerArea(this.$refs.imgRef);
        // attach an event handler to assign annotated image back to our image element
        markerArea.addRenderEventListener(dataUrl => {
          this.$refs.imgRef.src = dataUrl;
        });
        // launch marker.js
        markerArea.show();  
      },
      navigateLeft(){
        this.pageNumber = this.pageNumber - 1;
      },
      navigateRight(){
          this.pageNumber = this.pageNumber + 1;
      }
    }
   

}
</script>

